@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;600;700&family=Lexend+Deca:wght@100;200;300;400;500;600;700&family=Poppins:wght@300;400;600&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

html{
  font-size: 62.5%;
}
h1{
  text-align: center;
  font-size: 40px;
  margin-top: 50px;
  margin-bottom: 20px;
}
p{
  text-align: center;
  font-size: 20px;
  
}

.table-container {
  overflow-x: auto;
  margin: 20px;

}
table {
  width: 100%;
  border-collapse: collapse;
  margin: 5rem auto;
  border-radius: 2rem;
  
  border-collapse: collapse;
  
}
thead{
  font-size: 1.8rem;
 
  color: #000000;
}

th,
td {
  font-size: 1.8rem;
  text-align: center;
  color: #000000;
  padding: 10px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

th {
  background-color: #f2f2f2;
  font-weight: bold;
}




@media screen and (max-width: 600px) {
  table {
    display: block;
    overflow-x: auto;
    white-space: nowrap;
  }

  th,
  td {
    display: inline-block;
    width: auto;
  }
}













